import React from 'react';
import blackfist from "./black-fist.svg";
import './home.css';

function Navigation() {
    return (
        <nav className="menu navbar navbar-expand-lg navbar-dark">
            <img src={blackfist} className="d-inline-block img-fluid blm" alt="BLM" width="32"/>
            <span className="navbar-brand font-weight-bold mb-0 h1">Black Lives Matter</span>
            <button className="navbar-toggler" type="button" data-toggle="collapse"
                    data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup"
                    aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                    <a className="top-link nav-item nav-link active" href="#">
                        Home <span className="sr-only">(current)</span>
                    </a>
                    <a className="top-link nav-item nav-link" href="#resume">
                        Resume <span className="sr-only">(current)</span>
                    </a>
                </div>
            </div>
        </nav>
    );
}

class Home extends React.Component {
    render() {
        return (
            <div className="row">
                <div className="col-12">
                    <Navigation/>
                </div>
                <div className="col-12 text-center text-light jumbotron jumbotron-fluid bg-dark">
                    <div className="site-title">LR's Solutions</div>
                    <p className="site-subtitle lead text-truncate">For every problem, there's a solution.</p>
                </div>
            </div>
        );
    }
}

export default Home;